import {
  Button,
  FormControl,
  InputLabel,
  MenuItem,
  Select,
  Typography,
} from '@mui/material';
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { api } from 'src/views/api';
import useStyles from './Filter.styles';

const Filter = ({ filterFunction, getFilteredProducts }) => {
  const classes = useStyles();
  const [categories, setcategories] = useState([]);
  const [parentCategory, setParentCategory] = useState('');
  const [childCategory, setChildCategory] = useState('');
  const [parentFilter, setParentFilter] = useState('');
  const [filters, setFilters] = useState([]);
  const userId = localStorage.userId;

  useEffect(() => {
    (async () => {
      const categories = await axios.get(`${api}/api/categories`);
      setcategories(categories.data?.categoryList);
    })();
  }, []);

  const handleFilter = async (e) => {
    e.preventDefault();
    if (childCategory) {
      const data = {
        categories: childCategory,
        filters: Object.entries(parentFilter).map(([key, value]) => ({
          name: key,
          value: value,
        })),
      };

      if (localStorage.role === 'admin') {
        const response = await axios.post(
          `${api}/api/product/filter/${userId}`,
          data
        );
        getFilteredProducts(response.data);
      } else {
        const response = await axios.post(`${api}/api/product/filter`, data);
        getFilteredProducts(response.data);
      }
    } else {
      if (localStorage.role === 'admin') {
        const response = await axios.get(`${api}/api/products/${userId}`);
        getFilteredProducts(response.data);
      } else {
        const response = await axios.get(`${api}/api/products`);
        getFilteredProducts(response.data);
      }
    }
  };

  return (
    <div className={classes.filter}>
      <Typography variant='h6'>فیلتر</Typography>
      <div className={classes.filterRow}>
        <FormControl className={classes.formControl}>
          <InputLabel id='demo-simple-select-label'>دسته بندی</InputLabel>
          <Select
            onChange={(e) => setParentCategory(e.target.value)}
            value={parentCategory}
          >
            {categories?.map((category) => (
              <MenuItem key={category?._id} value={category?._id}>
                {category?.name}
              </MenuItem>
            ))}
          </Select>
        </FormControl>
      </div>
      {parentCategory && (
        <div className={classes.filterRow}>
          <FormControl className={classes.formControl}>
            <InputLabel id='demo-simple-select-label'>زیر دسته بندی</InputLabel>
            <Select
              onChange={async (e) => {
                const response = await axios.get(
                  `${api}/api/filter/category/${e.target.value}`
                );
                setFilters(response?.data);
                setChildCategory(e.target.value);
              }}
              value={childCategory}
            >
              {categories
                ?.filter((item) => item?._id === parentCategory)?.[0]
                ?.children?.map((category) => (
                  <MenuItem key={category?._id} value={category?._id}>
                    {category?.name}
                  </MenuItem>
                ))}
            </Select>
          </FormControl>
        </div>
      )}
      {filters?.length > 0 &&
        childCategory &&
        filters?.map((filter) => (
          <div className={classes.filterRow}>
            <FormControl className={classes.formControl}>
              <InputLabel id='demo-simple-select-label'>
                {filter?.name}
              </InputLabel>
              <Select
                onChange={(e) => {
                  if (e.target.value.length) {
                    setParentFilter({
                      ...parentFilter,
                      [filter?.name?.trim()]: e.target.value,
                    });
                  } else {
                    delete parentFilter[filter?.name?.trim()];
                  }
                }}
                value={parentFilter?.[filter?.name]}
              >
                <MenuItem value=''>انتخاب کنید</MenuItem>
                {filter?.values?.map((value) => (
                  <MenuItem key={value?._id} value={value?._id}>
                    {value?.name}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </div>
        ))}
      <div className={classes.filterRow}>
        <Button
          variant='contained'
          color='secondary'
          fullWidth
          onClick={handleFilter}
          disabled={!childCategory}
        >
          <Typography variant='h6'>اعمال فیلتر</Typography>
        </Button>
      </div>
    </div>
  );
};

export default Filter;
