import {
  cilLockLocked,
  // cilSettings,
  // cilTask,
  cilUser
} from '@coreui/icons';
import CIcon from '@coreui/icons-react';
import {
  CAvatar,
  // CBadge,
  CDropdown,
  CDropdownDivider,
  CDropdownHeader,
  CDropdownItem,
  CDropdownMenu,
  CDropdownToggle
} from '@coreui/react';
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min';
import { api } from 'src/views/api';

const AppHeaderDropdown = () => {
  const [avatar, setAvatar] = useState();
  const { push } = useHistory();

  useEffect(async () => {
    try {
      const response = await axios.get(`${api}/api/user/me`, {
        headers: {
          'x-auth-token': localStorage.token,
        },
      });
      setAvatar(response?.data?.avatar);
    } catch (error) {
      push('/login');
    }
  }, []);

  console.log(avatar)
  return (
    <CDropdown variant='nav-item'>
      <CDropdownToggle placement='bottom-end' className='py-0' caret={false}>
          <CAvatar
            src={avatar ? `${api}/uploads/customer/${localStorage.userId}/${avatar}` : "https://www.pphfoundation.ca/wp-content/uploads/2018/05/default-avatar-600x600.png"}
            size='md'
          />
      </CDropdownToggle>
      <CDropdownMenu className='pt-0' placement='bottom-end'>
        <CDropdownHeader className='bg-light fw-semibold py-2'>
          تنظیمات پروفایل
        </CDropdownHeader>
        <CDropdownItem href='/#/profile'>
          <CIcon icon={cilUser} className='me-2' />
          پروفایل کاربری
        </CDropdownItem>
        <CDropdownDivider />
        <CDropdownItem href='/#/login' onClick={() => {
          if(localStorage.role === 'super_admin') {
            localStorage.clear()
          } else {
            window.open('http://45.92.95.60:8080/', '_blank')
          }
        }}>
          <CIcon icon={cilLockLocked} className='me-2' />
          خروج
        </CDropdownItem>
      </CDropdownMenu>
    </CDropdown>
  );
};

export default AppHeaderDropdown;
